import React, { useState } from 'react';
import PageTemplate from '../PageTemplate/PageTemplate';
import ProductSlider from '../../components/ProductSlider/ProductSlider';
import './ProductPageDescription.css';

function ProductPageDescription({ product }) {
    const [selectedOption, setSelectedOption] = useState(product.options[0]);
    const [quantity, setQuantity] = useState(1); //  

    const handleOptionChange = (option) => {
        setSelectedOption(option);
        setQuantity(1); //     
    };

    const handleQuantityChange = (delta) => {
        setQuantity(prevQuantity => Math.max(1, prevQuantity + delta)); //     1
    };

    //   
    const totalPrice = selectedOption.price * quantity;

    return (
        <div className="product-page-description">
            <PageTemplate>
                <div className="product-slider-wrapper">
                    <ProductSlider images={product.slider} />
                    <div>
                        <h1>{product.title}</h1>
                        <div className="page-underline"></div>
                        <div className="product-price">
                            <h3>{totalPrice}</h3>
                            {selectedOption.markdown && (
                                <h3>
                                    <s>{selectedOption.markdown}</s>
                                </h3>
                            )}
                        </div>
                        <p>{product.additionalDescription}</p>
                        <ul>
                            {product.options.map(option => (
                                <li key={option.variant}>
                                    <button
                                        className={selectedOption.variant === option.variant ? 'active' : ''}
                                        onClick={() => handleOptionChange(option)}
                                    >{option.variant}</button>
                                </li>
                            ))}
                        </ul>
                        <div className="quantity-selector">
                            <div className="quantity-selector-nmbr">{quantity}</div>
                            <div className="quantity-selector-buttons">
                                <button onClick={() => handleQuantityChange(1)}></button>
                                <button onClick={() => handleQuantityChange(-1)}></button>
                            </div>
                            <p>Choose a quantity</p>
                        </div>
                        <button className='btn btn-add-chart'>Add to Cart <img src={`${process.env.PUBLIC_URL}/images/icons/shopping-cart-white.svg`} alt="" /></button>
                    </div>
                </div>
                <div className="description-container">
                    <div className="description-container-title">Description</div>
                    <div className="description-block">
                        <h1>Product Description</h1>
                        <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Quo suscipit ducimus fuga temporibus at veritatis, odit beatae, deserunt dignissimos voluptatum tenetur quisquam molestiae quod nostrum voluptatem voluptas, commodi illum esse.</p>
                        <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Quo suscipit ducimus fuga temporibus at veritatis, odit beatae, deserunt dignissimos voluptatum tenetur quisquam molestiae quod nostrum voluptatem voluptas, commodi illum esse.</p>
                        <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Quo suscipit ducimus fuga temporibus at veritatis, odit beatae, deserunt dignissimos voluptatum tenetur quisquam molestiae quod nostrum voluptatem voluptas, commodi illum esse.</p>
                        <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Quo suscipit ducimus fuga temporibus at veritatis, odit beatae, deserunt dignissimos voluptatum tenetur quisquam molestiae quod nostrum voluptatem voluptas, commodi illum esse.</p>
                    </div>
                </div>

            </PageTemplate>
        </div>
    );
}

export default ProductPageDescription;
